import { mapActions, mapMutations } from "vuex";
import { getRemd } from "@/api/getData";

export default {
  data() {
    return {
      loading: true,
      remdList: "",
      currentID: ""
    };
  },
  methods: {
    ...mapActions(["getListDetail"]),
    ...mapMutations(["setLoad"]),
    async gotoListDetail(item) {
      if (this.currentID != item.id) {
        this.setLoad(true);
        try {
          let res = await this.getListDetail(item.id);
          this.currentID = item.id;
          this.$router.push({
            path: "/playlist/detail",
            query: { id: item.id }
          });
        } catch (err) {
          alert("");
        } finally {
          this.setLoad(false);
        }
      } else {
        this.$router.push({
          path: "/playlist/detail",
          query: { id: item.id }
        });
      }
    },
    parseData(response) {
      let result = response.data.result.slice(0, 6).map((currentValue) => {
        let obj = {
          id: currentValue.id,
          name: currentValue.name,
          imgUrl: currentValue.picUrl
        };
        let temp = parseInt(currentValue.playCount) + "";
        if (temp.length >= 6) {
          obj.play = temp[0] + temp[1] + "";
        } else {
          obj.play = temp;
        }
        return obj;
      });
      return {
        one: result.slice(0, 3),
        two: result.slice(3)
      };
    }
  },
  async created() {
    try {
      let response = await getRemd();
      this.remdList = this.parseData(response);
    } catch {
      alert("");
    } finally {
      this.loading = false;
    }
  }
};
