import Song from "@/components/song.vue";
import { getRank } from "@/api/getData";

export default {
  data() {
    return {
      loading: true,
      error: false,
      rank: []
    };
  },
  components: {
    Song
  },
  computed: {
    getTime() {
      let d = new Date();
      let month = d.getMonth();
      let day = d.getDate();
      return `${month + 1}${day}`;
    }
  },
  methods: {
    parseData(response) {
      let song = response.data.playlist.tracks.slice(0, 20);

      let rankListData = song.map(function(currentValue, index) {
        let artistsName = "";
        if (currentValue.ar.length >= 2) {
          //
          artistsName = currentValue.ar[0].name + "/" + currentValue.ar[1].name;
        } else {
          artistsName = currentValue.ar[0].name;
        }
        let obj = {
          id: currentValue.id,
          title: currentValue.name,
          alias: currentValue.alia[0],
          artists: artistsName,
          album: currentValue.al.name,
          rank: index + 1
        };
        if (index <= 2) {
          //
          obj.color = true;
        }
        if (index <= 8) {
          //90
          obj.rank = "0" + obj.rank;
        }
        return obj;
      });

      return rankListData;
    }
  },
  async created() {
    try {
      let response = await getRank();
      this.rank = this.parseData(response);
    } catch {
      this.error = true;
    } finally {
      this.loading = false;
    }
  }
};
